import React, { Component } from 'react';
import { Modal, Form, Input, Switch } from 'antd';
import {formItemLayout} from '../../helpers/config/formConfig'
import { noti } from '../../helpers/utils';
const { Item } = Form;

class UpdateStatusAuctionRegister extends Component {

    handleUpdate = (e) => {
        this.props.form.validateFields((err, values) => {
            if (!err) {
                // onUpdateStatus
                const { status } = values;
                let statusStr = 'PROGRESS'
                if (status){
                    statusStr = 'READY'
                }
                let data = {
                    userId: this.props.data.userId,
                    auctionId:this.props.data.auctionId,
                    status: statusStr
                }
                let res = this.props.onUpdateStatus(data);
                res.then(
                    (result) => {
                        if(result.data.status === 200){
                            this.props.onUpdateUserComplete()
                            this.props.form.resetFields()
                        }
                    },
                    (error) => { 
                        var mess = ""
                        if(error.response.data.message){
                            mess = error.response.data.message
                        }
                        noti.error("Khng th cp nht ngi dng. " + mess)
                    }
                )
            }
        })
    }

    afterClose = () => {
        
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const { displayName, phoneNumber, status, visible } = this.props.data;

        return (<Modal
            title={"Cp nht trng thi ng k phin u gi"}
            visible={visible}
            confirmLoading={this.props.loading}
            cancelText="ng"
            destroyOnClose={true}
            afterClose={this.afterClose}
            onCancel={this.props.onCancel}
            onOk={this.handleUpdate}
            okText="Cp Nht">
                <Form>
                    <Item {...formItemLayout} label='H v Tn'>{
                        getFieldDecorator('displayName', {
                            initialValue: displayName,
                            rules: [{ required: true, message: '' }]
                    })(<Input placeholder="H v Tn" readOnly={true} />)}</Item>

                    <Item {...formItemLayout} label='S in thoi'>{
                        getFieldDecorator('phoneNumber', {
                            initialValue: phoneNumber,
                            rules: [{ required: true, message: '' }]
                    })(<Input placeholder="S in thoi" readOnly={true} />)}</Item>

                    <Item {...formItemLayout} label='Trng thi'>{
                        getFieldDecorator('status', {
                            rules: [{ required: true, message: 'Cp nht gi tr' }]
                    })(<Switch defaultChecked={'READY'===status} />)}</Item>

                </Form>
          </Modal>);
    }

}
export default Form.create()(UpdateStatusAuctionRegister);