import React, { Component } from 'react';
import { Modal, Form, Input } from 'antd';
import {formItemLayout} from '../../helpers/config/formConfig'
import UploadAvatarImage from "../common/UploadAvatarImage";
import { noti } from '../../helpers/utils';
const { Item } = Form;

class NewUser extends Component {
    avatarUrl = ""
    handleCreateNew = (e) => {
        this.props.form.validateFields((err, values) => {
            const { displayName, phoneNumber, address, password, bankAccount, bankName, identity } = values;
            let avatar = this.avatarUrl
            if (!err) {
                let data = {
                    displayName:displayName,
                    phoneNumber:phoneNumber,
                    address:address,
                    avatar:avatar,
                    password:password,
                    bankAccount:bankAccount,
                    bankName:bankName,
                    identity:identity,
                    isActive:true
                }
                let res = this.props.onCreateNew(data);
                res.then(
                    (result) => {
                        if(result.data.status === 200){
                            this.props.onCreateNewComplete()
                            this.props.form.resetFields()
                        }
                    },
                    (error) => { 
                        var mess = ""
                        if(error.response.data.message){
                            mess = error.response.data.message
                        }
                        noti.error("Khng th to ngi dng. " + mess)
                    }
                )
            }
        });
    }
    
    onUploadComplete = (imageFileList) => {
        if(imageFileList.length > 0){
            this.avatarUrl = imageFileList[0].url;
        }
    }

    handleUploadImages = (imageFileList) => {
        this.setState({ imageFileList});
    }

    onUploadRemove = () => {
        this.avatarUrl  = "";
    }
 
    render() {
        const { getFieldDecorator } = this.props.form;
        const { visible } = this.props.data;
        return (<Modal
            title={"To ngi dng"}
            visible={visible}
            confirmLoading={this.props.loading}
            cancelText="ng"
            destroyOnClose={true}
            onCancel={this.props.onCancel}
            onOk={this.handleCreateNew}
            okText="Thm">
                <Form>
                    <Item {...formItemLayout} label='nh i din'>
                        <UploadAvatarImage 
                            handleUploadImages={this.handleUploadImages} 
                            onUploadComplete={this.onUploadComplete} fileList={[]}
                            onUploadRemove={this.onUploadRemove}
                        />
                    </Item>

                    <Item {...formItemLayout} label='H v Tn'>{
                        getFieldDecorator('displayName', {
                            initialValue: '',
                            rules: [{ required: true, message: 'Vui lng nhp h tn' }]
                    })(<Input placeholder="displayName" readOnly={false} />)}</Item>

                    <Item {...formItemLayout} label='S in thoi'>{
                        getFieldDecorator('phoneNumber', {
                            initialValue: '',
                            rules: [{ required: true, message: 'Vui lng nhp s in thoi' }]
                    })(<Input placeholder="S in thoi" readOnly={false} />)}</Item>

                    <Item {...formItemLayout} label='CMND/CCCD'>{
                        getFieldDecorator('identity', {
                            initialValue: '',
                            rules: [{ required: true, message: '' }]
                    })(<Input placeholder="CMND/CCCD" readOnly={false} />)}</Item>

                    <Item {...formItemLayout} label='a ch'>{
                        getFieldDecorator('address', {
                            initialValue: '',
                            rules: [{ required: true, message: 'Vui lng nhp a ch' }]
                    })(<Input placeholder="a ch" readOnly={false} />)}</Item>

                    <Item {...formItemLayout} label='Mt khu'>{
                        getFieldDecorator('password', {
                            initialValue: '',
                            rules: [{ required: true, message: 'Vui lng nhp mt khu' }]
                    })(<Input.Password placeholder="Mt khu" readOnly={false} />)}</Item>

                    <Item {...formItemLayout} label='S TK ngn hng'>{
                        getFieldDecorator('bankAccount', {
                            initialValue: '',
                            rules: [{ required: false, message: '' }]
                    })(<Input placeholder="Ti khon ngn hng" readOnly={false} />)}</Item>

                    <Item {...formItemLayout} label='Tn ngn hng'>{
                        getFieldDecorator('bankName', {
                            initialValue: '',
                            rules: [{ required: false, message: '' }]
                    })(<Input placeholder="Tn ngn hng" readOnly={false} />)}</Item>

                    {/* <Item {...formItemLayout} label='Kch hot'>{
                        getFieldDecorator('isActive', {
                            initialValue: true ,
                            rules: [{ required: false, message: '' }]
                    })(<Checkbox defaultChecked={true}>Kch hot khi to </Checkbox>)}</Item> */}
                </Form>
          </Modal>);
    }

}
export default Form.create()(NewUser);