import React, { Component } from 'react';
import { Modal ,Button } from 'antd';

const { confirm } = Modal;

class DeleteUser extends Component {

    handleShowConfirm = () => {
        const { onOk, onCancel, data } = this.props;
        confirm({
            title: 'Bn mun xo ngi dng ' + data.displayName,
            content: <div>
                <p>Tt c thng tin ca <b>{data.displayName}</b> s b mt</p>
                <p>Bn chc chn mun xo <b>{data.displayName}</b>?</p>
            </div>,
            onOk,
            onCancel,
            okButtonProps: { type: "danger", icon: "delete" },
            okText: "Delete"
          });
    }

    render() {
        return (<Button className="mr5" icon="delete" type="danger" disabled={this.props.disabled} onClick={this.handleShowConfirm} />);
    }
}

export default DeleteUser;
